"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Textarea } from "@/components/ui/textarea";

export function AIContentManager() {
  const [topic, setTopic] = useState("");
  const [keywords, setKeywords] = useState("");
  const [generatedContent, setGeneratedContent] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  const handleGenerateContent = async () => {
    if (!topic || !keywords) {
      setError("Please provide both topic and keywords");
      return;
    }

    setIsLoading(true);
    setError("");

    try {
      const response = await fetch("/api/joomla-ai/content-generation", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ topic, keywords }),
      });

      if (!response.ok) {
        throw new Error("Failed to generate content");
      }

      const data = await response.json();
      setGeneratedContent(data.content);
    } catch (err) {
      setError("Failed to generate content. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">AI Content Generator</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="topic" className="text-sm font-medium">
              Topic
            </label>
            <Input
              id="topic"
              placeholder="Enter the main topic..."
              value={topic}
              onChange={(e) => setTopic(e.target.value)}
            />
          </div>

          <div className="space-y-2">
            <label htmlFor="keywords" className="text-sm font-medium">
              Keywords
            </label>
            <Input
              id="keywords"
              placeholder="Enter keywords (comma-separated)..."
              value={keywords}
              onChange={(e) => setKeywords(e.target.value)}
            />
          </div>

          <Button
            onClick={handleGenerateContent}
            disabled={isLoading}
            className="w-full"
          >
            {isLoading ? "Generating..." : "Generate Content"}
          </Button>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {generatedContent && (
            <div className="space-y-2">
              <label className="text-sm font-medium">Generated Content</label>
              <Textarea
                value={generatedContent}
                readOnly
                className="min-h-[200px]"
              />
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

export default AIContentManager;
