"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Textarea } from "@/components/ui/textarea";
import { Progress } from "@/components/ui/progress";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

interface SEOAnalysis {
  score: number;
  recommendations: Array<{
    type: "success" | "warning" | "error";
    message: string;
  }>;
  metrics: {
    wordCount: number;
    readabilityScore: number;
    keywordDensity: number;
  };
}

export function AISEOOptimizer() {
  const [content, setContent] = useState("");
  const [targetKeyword, setTargetKeyword] = useState("");
  const [analysis, setAnalysis] = useState<SEOAnalysis | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [error, setError] = useState("");

  const handleAnalyze = async () => {
    if (!content || !targetKeyword) {
      setError("Please provide both content and target keyword");
      return;
    }

    setIsAnalyzing(true);
    setError("");

    try {
      const response = await fetch("/api/joomla-ai/seo-analysis", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ content, targetKeyword }),
      });

      if (!response.ok) {
        throw new Error("Failed to analyze content");
      }

      const data = await response.json();
      setAnalysis(data);
    } catch (err) {
      setError("Failed to analyze content. Please try again.");
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">AI SEO Optimizer</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="keyword" className="text-sm font-medium">
              Target Keyword
            </label>
            <input
              id="keyword"
              className="w-full px-3 py-2 border rounded-md"
              placeholder="Enter your target keyword..."
              value={targetKeyword}
              onChange={(e) => setTargetKeyword(e.target.value)}
            />
          </div>

          <div className="space-y-2">
            <label htmlFor="content" className="text-sm font-medium">
              Content to Analyze
            </label>
            <Textarea
              id="content"
              placeholder="Paste your content here..."
              value={content}
              onChange={(e) => setContent(e.target.value)}
              className="min-h-[200px]"
            />
          </div>

          <Button
            onClick={handleAnalyze}
            disabled={isAnalyzing}
            className="w-full"
          >
            {isAnalyzing ? "Analyzing..." : "Analyze SEO"}
          </Button>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {analysis && (
            <div className="space-y-6">
              <div className="space-y-2">
                <h3 className="text-lg font-medium">Overall SEO Score</h3>
                <Progress value={analysis.score} className="w-full" />
                <p className="text-sm text-gray-500">
                  Score: {analysis.score}/100
                </p>
              </div>

              <div className="space-y-2">
                <h3 className="text-lg font-medium">Key Metrics</h3>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Metric</TableHead>
                      <TableHead>Value</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    <TableRow>
                      <TableCell>Word Count</TableCell>
                      <TableCell>{analysis.metrics.wordCount}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell>Readability Score</TableCell>
                      <TableCell>{analysis.metrics.readabilityScore}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell>Keyword Density</TableCell>
                      <TableCell>{analysis.metrics.keywordDensity}%</TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              </div>

              <div className="space-y-2">
                <h3 className="text-lg font-medium">Recommendations</h3>
                <div className="space-y-2">
                  {analysis.recommendations.map((rec, index) => (
                    <Alert
                      key={index}
                      variant={rec.type === "error" ? "destructive" : "default"}
                    >
                      <AlertDescription>{rec.message}</AlertDescription>
                    </Alert>
                  ))}
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

export default AISEOOptimizer;
