"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";

interface DataSource {
  id: string;
  name: string;
  url: string;
  type: "rss" | "api" | "webpage";
  status: "active" | "error" | "pending";
  lastUpdate: string;
  frequency: string;
}

interface DataPreview {
  title: string;
  content: string;
  timestamp: string;
}

export function AIDataIntegrator() {
  const [sources, setSources] = useState<DataSource[]>([]);
  const [sourceUrl, setSourceUrl] = useState("");
  const [sourceType, setSourceType] = useState<string>("");
  const [updateFrequency, setUpdateFrequency] = useState<string>("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [preview, setPreview] = useState<DataPreview[]>([]);
  const [selectedSource, setSelectedSource] = useState<string | null>(null);

  const sourceTypes = [
    { id: "rss", name: "RSS Feed" },
    { id: "api", name: "REST API" },
    { id: "webpage", name: "Web Page" },
  ];

  const frequencies = [
    { id: "5min", name: "Every 5 minutes" },
    { id: "15min", name: "Every 15 minutes" },
    { id: "30min", name: "Every 30 minutes" },
    { id: "1hour", name: "Every hour" },
    { id: "6hours", name: "Every 6 hours" },
    { id: "12hours", name: "Every 12 hours" },
    { id: "24hours", name: "Every 24 hours" },
  ];

  const handleAddSource = async () => {
    if (!sourceUrl || !sourceType || !updateFrequency) {
      setError("Please fill in all fields");
      return;
    }

    setIsLoading(true);
    setError("");

    try {
      const response = await fetch("/api/joomla-ai/data-integrator/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          url: sourceUrl,
          type: sourceType,
          frequency: updateFrequency,
        }),
      });

      if (!response.ok) {
        throw new Error("Failed to add data source");
      }

      const data = await response.json();
      setSources((prev) => [...prev, data.source]);
      setSourceUrl("");
      setSourceType("");
      setUpdateFrequency("");
    } catch (err) {
      setError("Failed to add data source. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handlePreview = async (sourceId: string) => {
    setSelectedSource(sourceId);
    setIsLoading(true);
    setError("");

    try {
      const response = await fetch(
        `/api/joomla-ai/data-integrator/preview/${sourceId}`
      );

      if (!response.ok) {
        throw new Error("Failed to fetch preview");
      }

      const data = await response.json();
      setPreview(data.preview);
    } catch (err) {
      setError("Failed to load preview. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const getStatusBadge = (status: DataSource["status"]) => {
    switch (status) {
      case "active":
        return <Badge variant="secondary">Active</Badge>;
      case "error":
        return <Badge variant="destructive">Error</Badge>;
      case "pending":
        return <Badge variant="outline">Pending</Badge>;
      default:
        return <Badge variant="default">Unknown</Badge>;
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6 space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">AI Data Integrator</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <label className="text-sm font-medium">Source URL</label>
              <Input
                placeholder="Enter URL..."
                value={sourceUrl}
                onChange={(e) => setSourceUrl(e.target.value)}
              />
            </div>
            <div>
              <label className="text-sm font-medium">Source Type</label>
              <Select value={sourceType} onValueChange={setSourceType}>
                <SelectTrigger>
                  <SelectValue placeholder="Select type" />
                </SelectTrigger>
                <SelectContent>
                  {sourceTypes.map((type) => (
                    <SelectItem key={type.id} value={type.id}>
                      {type.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div>
              <label className="text-sm font-medium">Update Frequency</label>
              <Select value={updateFrequency} onValueChange={setUpdateFrequency}>
                <SelectTrigger>
                  <SelectValue placeholder="Select frequency" />
                </SelectTrigger>
                <SelectContent>
                  {frequencies.map((freq) => (
                    <SelectItem key={freq.id} value={freq.id}>
                      {freq.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          <Button
            onClick={handleAddSource}
            disabled={isLoading}
            className="w-full"
          >
            {isLoading ? "Adding Source..." : "Add Data Source"}
          </Button>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="space-y-4">
            <h3 className="text-lg font-medium">Data Sources</h3>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Name</TableHead>
                  <TableHead>Type</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Last Update</TableHead>
                  <TableHead>Frequency</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {sources.map((source) => (
                  <TableRow key={source.id}>
                    <TableCell>{source.name}</TableCell>
                    <TableCell className="capitalize">{source.type}</TableCell>
                    <TableCell>{getStatusBadge(source.status)}</TableCell>
                    <TableCell>{source.lastUpdate}</TableCell>
                    <TableCell>{source.frequency}</TableCell>
                    <TableCell>
                      <Button
                        onClick={() => handlePreview(source.id)}
                        variant="outline"
                        size="sm"
                      >
                        Preview Data
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          {selectedSource && preview.length > 0 && (
            <div className="space-y-4">
              <h3 className="text-lg font-medium">Data Preview</h3>
              <ScrollArea className="h-[400px] border rounded-lg p-4">
                {preview.map((item, index) => (
                  <Card key={index} className="mb-4">
                    <CardContent className="p-4">
                      <h4 className="font-medium">{item.title}</h4>
                      <p className="text-sm text-gray-500 mt-1">
                        {item.timestamp}
                      </p>
                      <p className="mt-2">{item.content}</p>
                    </CardContent>
                  </Card>
                ))}
              </ScrollArea>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

export default AIDataIntegrator;
