"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";

interface Plugin {
  id: string;
  name: string;
  version: string;
  latestVersion: string;
  status: "up-to-date" | "update-available" | "critical-update";
  description: string;
  author: string;
  lastUpdated: string;
}

export function AIPluginManager() {
  const [plugins, setPlugins] = useState<Plugin[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState("");
  const [updating, setUpdating] = useState<string | null>(null);
  const [updateProgress, setUpdateProgress] = useState(0);

  useEffect(() => {
    fetchPlugins();
  }, []);

  const fetchPlugins = async () => {
    try {
      const response = await fetch("/api/joomla-ai/plugin-manager");
      if (!response.ok) {
        throw new Error("Failed to fetch plugins");
      }
      const data = await response.json();
      setPlugins(data.plugins);
    } catch (err) {
      setError("Failed to load plugins. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleUpdate = async (pluginId: string) => {
    setUpdating(pluginId);
    setUpdateProgress(0);

    try {
      // Simulate progress updates
      const progressInterval = setInterval(() => {
        setUpdateProgress((prev) => Math.min(prev + 10, 90));
      }, 500);

      const response = await fetch(`/api/joomla-ai/plugin-manager/update`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ pluginId }),
      });

      clearInterval(progressInterval);

      if (!response.ok) {
        throw new Error("Update failed");
      }

      setUpdateProgress(100);
      setTimeout(() => {
        fetchPlugins();
        setUpdating(null);
        setUpdateProgress(0);
      }, 1000);
    } catch (err) {
      setError("Failed to update plugin. Please try again.");
      setUpdating(null);
      setUpdateProgress(0);
    }
  };

  const getStatusBadge = (status: Plugin["status"]) => {
    const labels = {
      "up-to-date": "Up to Date",
      "update-available": "Update Available",
      "critical-update": "Critical Update",
    };
    
    switch (status) {
      case "up-to-date":
        return <Badge variant="secondary">{labels[status]}</Badge>;
      case "update-available":
        return <Badge variant="outline">{labels[status]}</Badge>;
      case "critical-update":
        return <Badge variant="destructive">{labels[status]}</Badge>;
      default:
        return <Badge variant="default">{labels[status]}</Badge>;
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6 space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">AI Plugin Manager</CardTitle>
        </CardHeader>
        <CardContent>
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {isLoading ? (
            <div className="text-center py-4">Loading plugins...</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Plugin Name</TableHead>
                  <TableHead>Current Version</TableHead>
                  <TableHead>Latest Version</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {plugins.map((plugin) => (
                  <TableRow key={plugin.id}>
                    <TableCell>
                      <Dialog>
                        <DialogTrigger asChild>
                          <Button variant="link" className="p-0">
                            {plugin.name}
                          </Button>
                        </DialogTrigger>
                        <DialogContent>
                          <DialogHeader>
                            <DialogTitle>{plugin.name}</DialogTitle>
                            <DialogDescription>
                              <div className="space-y-2 mt-4">
                                <p>{plugin.description}</p>
                                <p>
                                  <strong>Author:</strong> {plugin.author}
                                </p>
                                <p>
                                  <strong>Last Updated:</strong>{" "}
                                  {plugin.lastUpdated}
                                </p>
                              </div>
                            </DialogDescription>
                          </DialogHeader>
                        </DialogContent>
                      </Dialog>
                    </TableCell>
                    <TableCell>{plugin.version}</TableCell>
                    <TableCell>{plugin.latestVersion}</TableCell>
                    <TableCell>{getStatusBadge(plugin.status)}</TableCell>
                    <TableCell>
                      {updating === plugin.id ? (
                        <div className="space-y-2">
                          <Progress value={updateProgress} className="w-[100px]" />
                          <p className="text-sm text-gray-500">
                            Updating... {updateProgress}%
                          </p>
                        </div>
                      ) : (
                        <Button
                          onClick={() => handleUpdate(plugin.id)}
                          disabled={plugin.status === "up-to-date"}
                          variant={
                            plugin.status === "critical-update"
                              ? "destructive"
                              : "default"
                          }
                          size="sm"
                        >
                          Update
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

export default AIPluginManager;
